<template>
<v-expansion-panel style="width: 100%">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{item_obj.id}}</label>
            </v-col>
            <v-col>
                {{item_obj.question_text}}
            </v-col>

        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
        <v-expansion-panels>
            <!-- leerdoel id -->
            <v-expansion-panel>
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel Id:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <p class="box">{{item_obj.target_id}}</p>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- options -->
            <v-expansion-panel v-if="Object.keys(item_obj).includes('answer_options')">
                <v-expansion-panel-title class="ma-0">
                    <p>Keuzes:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    #
                                </th>
                                <th class="left-text">
                                    Option
                                </th>
                                <th class="left-text" v-if="Object.keys(item_obj).includes('answer_distribution')">
                                Aantal
                                </th>
                                <th class="left-text" v-if="Object.keys(item_obj).includes('answer_distribution')">
                                    %
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(option, index) in item_obj.answer_options" :key="index" :style="{'background-color': option.is_correct ? '#afff7d': 'white'}">
                                    <td>
                                        <span>{{option.id}}</span>
                                    </td>
                                    <td>{{option.text}}</td>
                                    <td v-if="Object.keys(item_obj).includes('answer_distribution')">
                                        <span>{{item_obj.answer_distribution[option.id]}}</span>
                                    </td>
                                    <td v-if="Object.keys(item_obj).includes('answer_distribution')">
                                        <span class="text-right">{{Math.round(item_obj.answer_distribution[option.id] / totalAnswerDistribution(item_obj.answer_distribution) * 100)}}</span>
                                    </td>
                            </tr>

                        </tbody>
                    </v-table>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- tijd data -->
            <v-expansion-panel v-if="Object.keys(item_obj).includes('time_estimate')">
                <v-expansion-panel-title class="ma-0">
                    <p>Tijd Data</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text>
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    <p>Type</p>
                                </th>
                                <th class="left-text">
                                    <p>Data</p>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- <div v-if="'time_estimate' in item_obj.description"> -->
                                <tr v-for="(key, index) in Object.keys(item_obj.time_estimate)" :key="index">
                                    <td>{{key}}</td>
                                    <td>{{parseFloat(item_obj.time_estimate[key]).toFixed(2)}}s</td>
                                </tr>
                            <!-- </div> -->
                        </tbody>
                    </v-table>
                </v-expansion-panel-text>
            </v-expansion-panel>
            <!-- target -->
            <v-expansion-panel v-if="!no_target">
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-expansion-panels>
                        <OutputListTarget :item_obj="this.$store.getters.targetDataFromId(item_obj.target_id)" />
                    </v-expansion-panels>
                </v-expansion-panel-text >
            </v-expansion-panel>
        </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import {defineAsyncComponent} from 'vue' 

export default {
    name: 'OutputListMcq',
    props: {
        item_obj: Object,
        no_target: {type:Boolean, default: false},
    },
    components: {
        OutputListTarget: defineAsyncComponent(() => import('./OutputListTarget.vue')) 
    },
    data (){
        return {
        }
    },
    computed: {
    },
    methods: {
        totalAnswerDistribution: function(answer_distribution){
            var total = 0
            Object.values(answer_distribution).forEach((e) => {
                total = total + Number(e)
            })
            return total
        }

    },
}
</script>

<style>
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    width: 100%;
    font-size: smaller;
    display: inline-block;
}
</style>