<template>
<div class="box">
    <v-row>
        <v-col>
            <v-btn @click="selectQuestionsFromTargets()" class="pa-2 ma-1" variant="outlined" :color="buttoncolor" size="h-auto">
                <span class="truncate">Vragen Bij Geselecteerde Leerdoelen</span>
            </v-btn>
        </v-col>
        <v-col>
            <v-btn @click="selectTargetsFromQuestions()" class="pa-2 ma-1" variant="outlined" :color="buttoncolor" size="h-auto">
                <span class="truncate">Leerdoelen Bij Geselecteerde Vragen</span>
            </v-btn>
        </v-col>
    </v-row>

    <hr noshade="true">
    <v-btn @click="selectReturnedData()" class="pa-2 ma-1" variant="outlined" :color="buttoncolor" size="h-auto">
        <span class="truncate">Selecteer Uitkomst</span>
    </v-btn>
</div>
</template>

<script>
export default {
    name: 'Selectors',
    data (){
        return {
            buttoncolor: '#3F816C', 
            selected_items: {'target': [], 'mcq': []},
        }
    },
    computed: {
        // used to check for chajge in selected items
        // used in: watch
        selectedItems (){
            return this.$store.getters.selectedItems
        },
        // returns the type of current selected tab
        // used in: selectedTabResponseArr, selectReturnedData
        selectedTabResponseType (){
            console.log(this.$store.getters.selectedTabQueryObj.returns)
            if (this.$store.getters.selectedTabQueryObj.returns === 'target' || this.$store.getters.selectedTabQueryObj.returns === 'target_list'){
                return 'target'
            }
            else {
                return 'mcq'
            }
        },
        // returns all the current selected ids and returned ids
        // used in: selectReturnedData
        selectedTabResponseArr (){
            // if (this.$store.getters.selectedTabQueryObj.path == 'most_difficult_question'){
            //     return [this.$store.getters.selectedTabQueryObj.response.Question_ID]
            // }
            if (this.$store.getters.selectedTabQueryObj.response instanceof Array){
                var new_selected = this.$store.getters.selectedTabQueryObj.response
            } else {
                var new_selected = [this.$store.getters.selectedTabQueryObj.response]
            }
            
            new_selected = new_selected.concat(this.$store.state.datapagina_module.selected_item_ids[this.selectedTabResponseType])
            
            return new_selected


        }

    },
    methods: {
        // sets the selected ids to current ids + returned
        // used in: template
        selectReturnedData(){
            if (this.outputListIds != []) {
                this.$store.commit('setSelectedItemIds', {value: this.selectedTabResponseArr, type: this.selectedTabResponseType})            
            }
        },
        selectQuestionsFromTargets(){
            if (this.selected_items['target'].length != 0){
                const question_ids = []
                this.selected_items['target'].forEach((e) => {Array.prototype.push.apply(question_ids, e.mcqs)})
                Array.prototype.push.apply(question_ids, this.$store.state.datapagina_module.selected_item_ids['mcq'])

                this.$store.commit('setSelectedItemIds',{value: question_ids, type: 'mcq'})
            }
        },
        selectTargetsFromQuestions(){
            if (this.selected_items['mcq'].length != 0){
                const target_ids = []
                this.selected_items['mcq'].forEach((e) => {if(!target_ids.includes(e.target_id)){
                                                                                target_ids.push(e.target_id)
                                                                                }})
                this.$store.commit('setSelectedItemIds',{value: target_ids, type: 'target'})
            }

        },

    },
    watch: {
        selectedItems (new_value){
            this.selected_items = Object.create(new_value)
        }
    }
}
</script>

