<template>
<v-expansion-panel style="width: 100%">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{target_data.id}}</label>
            </v-col>
            <v-col>
                {{target_data.label}}
            </v-col>

        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
        <div>
            <!-- if description is an string, show the string, if it's an obj sort and format the obj and show it nicer -->
                <v-expansion-panels>
                    <!-- description -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Beschrijving</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{target_data.description}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- vraag ids -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Vraag Ids</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{target_data.mcqs.toString()}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Youtube video -->
                    <v-expansion-panel v-if="Object.keys(target_data).includes('youtube_id')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Video</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text >
                            <iframe :src="'https://www.youtube.com/embed/' + target_data.youtube_id"  
                                    allow="accelerometer; 
                                            autoplay; 
                                            encrypted-media; 
                                            gyroscope; 
                                            picture-in-picture" 
                                    style="display: block; height:480px"
                                    width="100%"
                                    
                                    allowfullscreen></iframe>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Slide -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Slide</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <div v-if="Object.keys(target_data).includes('img_url_large')">
                                <img :src="target_data.img_url_large" alt="Target Photo" width="300">
                            </div>
                            <v-row  v-if="Object.keys(target_data).includes('slide_title')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Titel: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0">{{target_data.slide_title}}</span>
                                </v-col>
                            </v-row>
                            <hr  v-if="Object.keys(target_data).includes('slide_title') && Object.keys(target_data).includes('slide_text')">
                            <v-row v-if="Object.keys(target_data).includes('slide_text')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Text: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0">{{target_data.slide_text}}</span>
                                </v-col>
                            </v-row>

                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Questions Data -->
                    <v-expansion-panel v-if="Object.keys(target_data).includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Vragen</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-expansion-panels>
                                <QuestionPanel v-for="(mcqid, index) in target_data.mcqs" :key="index" :id="mcqid" :no_target="true" />
                            </v-expansion-panels>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Tijd Data -->
                    <v-expansion-panel v-if="Object.keys(target_data).includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Tijd Data</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-table>
                                <thead>
                                    <tr>
                                        <th class="left-text">
                                            <p>Type</p>
                                        </th>
                                        <th class="left-text">
                                            <p>Data</p>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!-- <div v-if="'time_estimate' in target_data.description"> -->
                                        <tr v-for="(key, index) in Object.keys(time_data)" :key="index">
                                            <td>{{key}}</td>
                                            <td>{{parseFloat(time_data[key]).toFixed(2)}}s</td>
                                        </tr>
                                    <!-- </div> -->
                                </tbody>
                            </v-table>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>

            </div>
    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import QuestionPanel from './QuestionPanel.vue'

export default {
    name: 'TargetPanel',
    components: {
        QuestionPanel
    },
    props: {
        id: Number,
    },
    computed: {
        target_data() {
            return this.$store.getters.targetDataFromId(this.id)
        },
        time_data(){
            var time_data = {}
            this.target_data.mcqs.forEach((e) => {
                if (Object.keys(e).includes('time_estimate')){
                    Object.keys(e.time_estimate).forEach((time_key) => {
                    if (!Object.keys(time_data).includes(time_key)){
                        time_data[time_key] = 0
                    }

                    time_data[time_key] = time_data[time_key] + Math.round(e.time_estimate[time_key])
                    })
                }

            });
            return time_data
        }
    },
    methods: {

    },  
    created() {
        this.$store.dispatch('getTargetDataFromArray', [this.id])
        this.$store.dispatch('getMcqDataFromArray', this.target_data.mcqs)
    },
}
</script>

<style>

</style>