<template>
<v-card style="border-color: #3F816C; border-width: 3px; padding: 3px;">
    <!-- tabs -->
    <v-card style="border-color: #3F816C; border-width: 1px;">
        <v-tabs v-model="selected_query_obj_path" style="height: min-content;" show-arrows centered center-active>
            <v-tab  v-for="(query_obj, index) in availableRequests" :key="index" :value="query_obj.path" >
                <span class="truncate" style="width: 105px; font-size: smaller;"  >{{query_obj.title}}</span>
            </v-tab>
        </v-tabs>
    </v-card >
    <!-- output window -->
    <v-window v-model="selected_query_obj_path" style="resize: vertical; height: 68vh; " >
        <div v-for="(query_obj, index) in availableRequests" :key="index">
            <v-window-item  :value="query_obj.path">
                <QueryOutputTab :query_obj="query_obj"/>
            </v-window-item>
        </div>
    </v-window>
</v-card>

</template>

<script>
import QueryOutputTab from './QueryOutputTab.vue';

export default {
    name: 'ResponseOutput',
    components: {
        QueryOutputTab
    },
    data (){
        return {
            selected_query_obj_path: 'similar_targets',
            buttoncolor: '#3F816C', 
        }
    },
    computed: {
        //shown responses
        // used in: template
        availableRequests(){
            return this.$store.state.datapagina_module.query_objects.filter((e) => {return e.shown == true})
        },
        // returns only the selected query obj
        // used in: template
        selectedQueryObj (){
            const index = this.$store.state.datapagina_module.query_objects.map((e) => {return e.path}).indexOf(this.selected_query_obj_path)
            if (index != -1) {
                return this.$store.state.datapagina_module.query_objects[index]
            }
            else {
                return undefined
            }
        },
        // returns last used query obj path
        // used in: lastRequestedPath (watch)
        lastRequestedPath(){
            return this.$store.state.datapagina_module.last_clicked_query_object_path
        },
    },
    methods: {

    },
    watch: {
        // returns last used query obj path
        // used in: selectedQueryObj
        lastRequestedPath(new_value, old_value){
            this.selected_query_obj_path = new_value
        },
        // used to set the store
        // used in: store
        selected_query_obj_path(new_value, old_value){
            this.$store.commit('setSelectedTabPath', new_value)
        }
    }
}
</script>

