<template>
<!-- create a button for every available (depends on data_input) request in veux store query_object-->
<div v-for="(query_object, index) in availableRequests" 
    :key="index" 
    style="padding: 3px" 
    size="h-auto">
    <!-- if the correct selected items, show, else, disabled -->
    <div v-if="(this.selectedItemsIdsLength == 1 && query_object.value_type == 'integer')
                || (this.selectedItemsIdsLength >= 1 && query_object.value_type == 'integer_array')"
        size="h-auto">

        <!-- firsts sets the input to the current ids, then creates a request that will output in the veux query_object.response -->
        <v-btn :loading="query_object.sending" 
                @click="sendRequest(query_object)"  
                variant="outlined" 
                :color="buttoncolor">
            <span class="truncate pa-2">{{query_object.title}}</span>
        </v-btn>
    </div>
    <!-- if the request is disabled (because you maybe selected too many), disable the button -->
    <div v-else>
        <v-btn disabled  
                variant="outlined" 
                :color="buttoncolor" 
                size="h-auto" >
            <span class="truncate pa-2">{{query_object.title}}</span>
        </v-btn>
    </div>
</div>
</template>

<script>
export default {
    name: 'Requesters',
    props: {
        request_type: String,
    },
    data (){
        return { 
            buttoncolor: '#3F816C', 

        }
    },
    computed: {
        //returns the length of selected items, used to know wich request is available
        // used in: template
        selectedItemsIdsLength (){
            return this.$store.state.datapagina_module.selected_item_ids[this.request_type].length
        },
        // returns all the object that have, shown: true and are the good type
        // used in: template
        availableRequests(){
            return this.$store.state.datapagina_module.query_objects.filter((e) => {return e.input == this.request_type && e.shown == true})
        },
    },
    methods: {
        // first set the query data
        // sends the query obj to the api
        // sumbit obj saves the reponse in itself
        // used in: template
        sendRequest: function(query_object){
            this.$store.commit('setQueryData', {'obj':query_object,
                                                'name': 'value', 
                                                'value': this.$store.state.datapagina_module.selected_item_ids[this.request_type]})
            this.$store.dispatch('submitObj', query_object)
        },

    },
}
</script>

