<template>
    <v-container class="ma-2 pa-2">
    <v-row>
        <v-col cols="12" md="6" sm="12">
            <v-row>
                <v-col>
                    <v-select :items="sort_types" 
                        v-model="selected_sort_type" 
                        style="" 
                        label="Selecteer type:" 
                        density="compact" 
                        outlined></v-select>
                    <v-checkbox-btn @click="toggleOptions(optionsWithData.map((e) => {return e.id}))" label="Toggle All" ></v-checkbox-btn>
                </v-col>
                <v-col>
                    <v-checkbox-btn label="Zonder Feedback" v-model="without_feedback"></v-checkbox-btn>
                </v-col>
            </v-row>
            
            <div v-if="selected_sort_type == 'Opties'">
                <OptionTable :options="optionsWithData" />
            </div>
            <div v-else-if="selected_sort_type == 'Vragen'">
                <QuestionPanels :options="optionsWithData" />
            </div>
            <div v-else-if="selected_sort_type == 'Leerdoel'">
                <TargetPanels :options="optionsWithData" />
            </div>
        </v-col>
        <v-col cols="12" md="6" sm="12">
            <v-list style="height: 400px" class="align-end containter list scrollable">
                <v-expansion-panels density="compact">
                    <v-expansion-panel v-for="(option, index) in selectedOptions" :key="index">
                        <v-expansion-panel-title>
                            <span>{{option.id}} {{option.text}}</span> <span style="float: right;">{{option.amount}}</span>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <div >
                                <v-row>
                                    <v-col cols="3" class="px-0">
                                        <span class="truncate">Vraag: </span>
                                    </v-col>
                                    <v-col cols="9" class="px-0">
                                        <span>{{this.$store.getters.questionDataFromId(option.question_id).label}}</span>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col cols="3" class="pr-0">
                                        <span class="truncate">Feedback: </span>
                                    </v-col>
                                    <v-col cols="9" class="ml-0">
                                        <v-textarea variant='outlined' density="compact" rows="3" v-model="feedback[option.id]" disabled></v-textarea>
                                        <v-btn variant="outlined" density="compact" class="ma-1" disabled>Sla Feedback Op</v-btn>
                                        <v-btn variant="outlined" density="compact" class="ma-1" disabled>Reset Feedback</v-btn>
                                    </v-col>
                                </v-row>
                            </div>
                        </v-expansion-panel-text>    

                    </v-expansion-panel>
                </v-expansion-panels>
            </v-list>
            <v-row>
                <v-sheet class="pa-0 my-4 ml-4" width="250px">
                    <v-text-field hide-details :model-value="selectedQuestions" variant="outlined" readonly class="pa-0"></v-text-field>
                </v-sheet>
                <v-sheet class="pa-2 my-4">
                    <v-btn @click="copyText(selectedQuestions)" >Copy ids</v-btn>
                </v-sheet>
            </v-row>
        </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'
import OptionTable from './components/OptionTable'
import QuestionPanels from './components/QuestionPanels.vue'
import TargetPanels from './components/TargetPanels.vue'
 
export default {
    name: 'Vrijwilliger',
    components: {
    OptionTable,
    QuestionPanels,
    TargetPanels
},
    data (){
        return {
            sort_types: [ 'Opties', 'Vragen', 'Leerdoel'],
            selected_sort_type: 'Opties',
            without_feedback: true,
            returned_data: {
                'rankingamount':[],
                'rankingnofeedback': []
            },
            feedback:{},
        }
    },
    computed:{
        // data_table_headers(){
        //     return [
        //         { text: 'id', value: 'id' },
        //         { text: this.selected_sort_type, value: 'text' },
        //         { text: 'Aantal Fout', value: 'amount' },
        //     ]
        // },
        filteredItems(){
            if (this.without_feedback == true){
                return this.returned_data['rankingnofeedback']
            } else {
                console.log(this.returned_data)
                const unsorted = this.returned_data['rankingamount']
                const unsorted_ids = unsorted.map(e => e.question)
                this.returned_data['rankingnofeedback'].forEach((e) => {
                    if (!unsorted_ids.includes(e.question)){
                        unsorted.push(e)
                    }
                })
                const sorted = unsorted.sort(function(a,b){
                    return b.amount - a.amount
                })
                return sorted
            }
        },
        optionsWithData(){
            const with_option_data = this.filteredItems.map((option) => {
                const option_data = this.splitQuestionAndOption(option.question)
                const option_text = this.$store.state.vrijwilliger_module.question_data[option_data.question]
                                                            .description
                                                            .options[option_data.option]
                return {'id': option.question, 'amount': option.amount, 'text': option_text,'feedback': 'Nog geen feedback', 'question_id': option_data.question, 'target_id': this.$store.getters.questionDataFromId(option_data.question).target_id}
            })
            return with_option_data
        },  
        selectedOptions(){
            return this.optionsWithData.filter((e) => {
                return this.$store.state.vrijwilliger_module.selected_options.ids.includes(e.id)
            })
        },
        selectedQuestions(){
            const question_ids = []
            this.selectedOptions.forEach((e) => {
                if (!question_ids.map(e => e.question_id).includes(e.question_id)){
                    question_ids.push(e.question_id)
                }
            })
            return question_ids
        },
    },
    methods: {

        getFrequentlyWrong: function(top_k=50, time_from = '1/1/2000'){
            
            axios.post('https://leerlevels-data.herokuapp.com/frequently_wrong', {
                "top-k": 200,
                "time_from": '1/1/2000'
            }).then(response => {
                console.log(response.data)
                this.returned_data = {
                    'rankingamount': Object.values(response.data.rankingamount),
                    'rankingnofeedback': Object.values(response.data.rankingnofeedback)
                }
            })
            .catch(function(error){
                console.log(error)
            })
        },
        splitQuestionAndOption: function(string){
            return {'option': string.charAt(string.length - 1).toLowerCase().charCodeAt(0) - 97, 'question': string.slice(0, -1)}
        },
        toggleOptions(ids){
            if (this.allSelected(ids) == true){
                const current_selected = this.$store.state.vrijwilliger_module.selected_options.ids
                this.$store.commit('setSelectedOptions', current_selected.filter((e) => {
                    return !ids.includes(e)
                }))
            } else {
                const new_selected_options = this.$store.state.vrijwilliger_module.selected_options.ids
                ids.forEach((e) => {
                    if (this.$store.state.vrijwilliger_module.selected_options.ids.indexOf(e) == -1){
                        new_selected_options.push(e)
                    }
                })
                this.$store.commit('setSelectedOptions', new_selected_options)
            }
        },
        allSelected: function(ids) {
            return ids.every((e) => {
                return this.$store.state.vrijwilliger_module.selected_options.ids.includes(e)
            })
        },
        copyText(text) {  
            const el = document.createElement('textarea');  
            el.value = text;                    
            el.setAttribute('readonly', '');                
            el.style.position = 'absolute';                     
            el.style.left = '-9999px';                      
            document.body.appendChild(el);                  
            const selected =  document.getSelection().rangeCount > 0  ? document.getSelection().getRangeAt(0) : false;                                    
            el.select();                                    
            document.execCommand('copy');                   
            document.body.removeChild(el);                  
            if (selected) {                                 
            document.getSelection().removeAllRanges();    
            document.getSelection().addRange(selected);   
            }
        },
    },
    mounted() {
        this.getFrequentlyWrong()
    },
    watch:{
        selected_sort_type: function(){
            this.getFrequentlyWrong()
        },
        without_feedback: function(){
            this.getFrequentlyWrong()
        }


    },

}
</script>

<style>
span {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    font-size: 93%;
}
</style>