<template>
<v-table>
    <thead>
        <tr>
            <th style="width: 6%">
                <v-checkbox-btn v-model="allSelected" @click="toggleAll"></v-checkbox-btn>
            </th>
            <th class="text-left" style="width: 6%">
                <span class="truncate" >id</span>
            </th>
            <th class="text-left">
                <span class="truncate">Options</span>
            </th>
            <th class="text-left">
                <span class="truncate"  style="float: right;margin-right: 20%;">Aantal Fout</span>
            </th>
        </tr>
    </thead>
    <tbody >
        <tr v-for="(item, index) in options" :key="index" @click="this.$store.commit('clickOption', item.id)">
            <td>
                <v-checkbox-btn v-model="selectedOptions" :value="item.id" readonly></v-checkbox-btn>
            </td>
            <td class="text-left"  style="width: 6%">
                <span class="truncate">{{item.id}}</span>
            </td>
            <td class="text-left">
                <span class="truncate">{{item.text}}</span>
            </td>
            <td class="text-left">
                <span class="truncate" style="float: right; margin-right: 20%;">{{item.amount}}</span>
            </td>
        </tr>
    </tbody>
</v-table>

</template>

<script>
export default {
    name: 'OptionTable',
    props: {
        options: Array,
    },
    computed: {
        selectedOptions: {
            set(value){this.$store.commit('setSelectedOptions', value)},
            get(){return this.$store.state.vrijwilliger.selected_options.ids}
        },
        allSelected:{
            get(){return this.options.every((e) => {
                return this.$store.state.vrijwilliger.selected_options.ids.includes(e.id)
            })},
            set(value){}
        }
    },
    methods: {
        toggleAll(){
            if (this.allSelected == true){
                const current_selected = this.$store.state.vrijwilliger.selected_options.ids
                const OptionIds = this.options.map((e) => {return e.id})
                this.$store.commit('setSelectedOptions', current_selected.filter((e) => {
                    return !OptionIds.includes(e)
                }))
            } else {
                const new_selected_options = this.$store.state.vrijwilliger.selected_options.ids
                this.options.forEach((e) => {
                    if (this.$store.state.vrijwilliger.selected_options.ids.indexOf(e.id) == -1){
                        new_selected_options.push(e.id)
                    }
                })
                this.$store.commit('setSelectedOptions', new_selected_options)
            }
        }
    },
}
</script>

