<template>
<v-expansion-panels variant="accordion" multiple>
    <v-expansion-panel v-for="target in targetsByOptions" :key="target.id">
        <v-expansion-panel-title>
            <v-checkbox-btn :model-value="allSelected(optionsByTargetId(target.id).map((e) => {return e.id}))" 
                                        density="compact" 
                                        @click="toggleOptions(optionsByTargetId(target.id).map((e) => {return e.id}))"                                        
                                        ></v-checkbox-btn>
            <span class="truncate" style="width: 10%">{{target.id}}</span>
            <span class="truncate" style="width: 85%">{{target.label}}</span>    
            <span class="truncate" style="width: 10%; float: right;">{{target.total_wrong}}</span>    
        </v-expansion-panel-title>
        <v-expansion-panel-text>
            <!-- to remove a self update crash -->
            <v-expansion-panels variant="accordion">
                <v-expansion-panel v-for="(question, index) in questionsByTarget(target.id)" :key="index">
                    <v-expansion-panel-title>
                        <v-checkbox-btn :model-value="allSelected(optionsByQuestionId(question.id).map((e) => {if (e)return e.id}))" 
                                        density="compact" 
                                        @click="toggleOptions(optionsByQuestionId(question.id).map((e) => {return e.id}))"                                        
                                        ></v-checkbox-btn>
                        <span class="truncate" style="width: 15%">{{question.id}}</span>
                        <span class="truncate" style="width: 85%">{{question.label}}</span>    
                        <span class="truncate" style="width: 10%; float: right;">{{question.total_wrong}}</span>    
                    </v-expansion-panel-title>
                    <v-expansion-panel-text>
                        <OptionTable :options="optionsByQuestionId(question.id)" />
                    </v-expansion-panel-text>
                </v-expansion-panel>
            </v-expansion-panels>

        </v-expansion-panel-text>
    </v-expansion-panel>
</v-expansion-panels>
</template>

<script>
import OptionTable from './OptionTable'

export default {
    name: 'TargetPanels',
    components:{
        OptionTable
    },
    props: {
        options: Array,
    },
    data(){
        return {
        }
    },
    computed:{
        questionsByOptions(){
            var questions = []
            this.options.forEach((option) => {
                var question = this.$store.getters.questionDataFromId(option.question_id)
                if (!questions.includes(question)){
                    question['total_wrong'] = option.amount
                    questions.push(question)
                } else {
                    const index = questions.indexOf(question)
                    questions[index].total_wrong += option.amount
                }
            })
            return questions.sort(
                function(a,b){
                    return b.total_wrong - a.total_wrong
                }
            )
        },
        targetsByOptions(){
            const targets = []
            this.options.forEach((option) => {
                var target = this.$store.getters.targetDataFromId(option.target_id)

                if (!targets.includes(target)){
                    target['total_wrong'] = option.amount
                    targets.push(target)
                } else {
                    const index = targets.indexOf(target)
                    targets[index].total_wrong += option.amount
                }
            })
            return targets.sort(
                function(a,b){
                    return b.total_wrong - a.total_wrong
                }
            )
        },

    },
    methods: {
        questionsByTarget(target_id){
            return this.questionsByOptions.filter((e) => {
                return e.target_id == target_id
            })
        },
        allSelected: function(ids) {
            return ids.every((e) => {
                return this.$store.state.vrijwilliger.selected_options.ids.includes(e)
            })
        },
        optionsByQuestionId: function(question_id){
            return this.options.filter((e) => {
                return e.question_id == question_id                
            })
        },    
        optionsByTargetId: function(target_id){
            return this.options.filter((e) => {
                return e.target_id == target_id                
            })
        },
        toggleOptions(ids){
            if (this.allSelected(ids) == true){
                const current_selected = this.$store.state.vrijwilliger.selected_options.ids
                this.$store.commit('setSelectedOptions', current_selected.filter((e) => {
                    return !ids.includes(e)
                }))
            } else {
                const new_selected_options = this.$store.state.vrijwilliger.selected_options.ids
                ids.forEach((e) => {
                    if (this.$store.state.vrijwilliger.selected_options.ids.indexOf(e) == -1){
                        new_selected_options.push(e)
                    }
                })
                this.$store.commit('setSelectedOptions', new_selected_options)
            }
        }
    },
}
</script>

