<template>
    <div v-if="dataLoaded == false">
        <v-progress-circular indeterminate></v-progress-circular>
    </div>
    <div v-else>
        <v-container class="ma-2 pa-2">
            <v-row no-gutters class="pa-0">
                <v-col class="pa-2 ma-0" cols="12" md="3" sm="11">
                    <div class="ma-2">
                        <h3>Selecteer Leerdoelen:</h3>
                        <SelectionList :selection_type="'target'" />
                        <h3>Vraag data op bij leerdoelen:</h3>
                        <Requesters :request_type="'target'" />
                    </div>
                </v-col>
                <v-col class="pa-2 ma-0"  cols="12" md="6" sm="11">
                    <div class="ma-0">
                        <h3>Snel Selecteren:</h3>
                        <Selectors />
                        <h3>Uitkomst:</h3>
                        <ResponseOutput/>
                    </div>
                </v-col>
                <v-col class="pa-2 ma-0"  cols="12" md="3" sm="11">
                    <div class="ma-2">
                        <h3>Selecteer Vragen:</h3>
                        <SelectionList :selection_type="'mcq'" />
                        <h3>Vraag data op bij vragen</h3>
                        <Requesters :request_type="'mcq'" />
                    </div>
                </v-col>
            </v-row>
        </v-container>
    </div>

</template>

<script>
import SelectionList from './components/SelectionList.vue'
import Requesters from './components/Requesters.vue'
import ResponseOutput from './components/ResponseOutput.vue'
import Selectors from './components/Selectors.vue'


export default {
    name: 'Datapagina',
    components: {
        SelectionList,
        Requesters,
        Selectors,
        ResponseOutput
    },
    data (){
        return {

        }
    },
    computed: {
        // checks if the target and mcq data is loaded
        dataLoaded(){
            return  [Object.keys(this.$store.state.datapagina_module.targets).length != 0, 
                    Object.keys(this.$store.state.datapagina_module.mcqs).length != 0].every(e => e === true)
        },
    },
    methods: {

    },
    mounted() {
        this.$store.dispatch('loadTargets')
        this.$store.dispatch('loadMcqs')
        
    },
}
</script>

