<template>
<v-expansion-panel style="width: 100%" v-if="render">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{mcq_data.id}}</label>
            </v-col>
            <v-col>
                {{mcq_data.question_text}}
            </v-col>

        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
        <v-expansion-panels>
            <!-- leerdoel id -->
            <v-expansion-panel>
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel Id:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <p class="box">{{mcq_data.target_id}}</p>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- options -->
            <v-expansion-panel v-if="Object.keys(mcq_data).includes('answer_options')">
                <v-expansion-panel-title class="ma-0">
                    <p>Keuzes:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    #
                                </th>
                                <th class="left-text">
                                    Option
                                </th>
                                <th class="left-text" v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                Aantal
                                </th>
                                <th class="left-text" v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                    %
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(option, index) in mcq_data.answer_options" :key="index" :style="{'background-color': option.is_correct ? '#afff7d': 'white'}">
                                    <td>
                                        <span>{{option.id}}</span>
                                    </td>
                                    <td>{{option.text}}</td>
                                    <td v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                        <span>{{mcq_data.answer_distribution[option.id]}}</span>
                                    </td>
                                    <td v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                        <span class="text-right">{{Math.round(mcq_data.answer_distribution[option.id] / totalAnswerDistribution(mcq_data.answer_distribution) * 100)}}</span>
                                    </td>
                            </tr>

                        </tbody>
                    </v-table>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- tijd data -->
            <v-expansion-panel v-if="Object.keys(mcq_data).includes('time_estimate')">
                <v-expansion-panel-title class="ma-0">
                    <p>Tijd Data</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text>
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    <p>Type</p>
                                </th>
                                <th class="left-text">
                                    <p>Data</p>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- <div v-if="'time_estimate' in mcq_data.description"> -->
                                <tr v-for="(key, index) in Object.keys(mcq_data.time_estimate)" :key="index">
                                    <td>{{key}}</td>
                                    <td>{{parseFloat(mcq_data.time_estimate[key]).toFixed(2)}}s</td>
                                </tr>
                            <!-- </div> -->
                        </tbody>
                    </v-table>
                </v-expansion-panel-text>
            </v-expansion-panel>
            <!-- target -->
            <v-expansion-panel v-if="!no_target">
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-expansion-panels>
                        <TargetPanel :id="mcq_data.target_id" />
                    </v-expansion-panels>
                </v-expansion-panel-text >
            </v-expansion-panel>
        </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import axios from 'axios'
import {defineAsyncComponent} from 'vue' 

export default {
    name: 'QuestionPanel',
    props: {
        id: {type:[Number, String], default: 0},
        obj: {type: Object, default: null},
        no_target: {type: Boolean, default: false},
    },
    components: {
        TargetPanel: defineAsyncComponent(() => import('./TargetPanel.vue'))
    },
    data (){
        return {
            render: true,
            mcq_data: {},
            query_objects: [
                {
                    path: "answer_distribution",
                    returns: 'object_list',
                    endpoint: "",
                    input: "mcq",
                    request_input: "question_list",
                    input_description: "Question Id's",
                    value: [], 
                    value_type: "integer_array",
                    title: "Question distribution by id list",
                    response: [],
                    sending: false,
                    send_ids: [],
                    shown: false,
                    is_request: true,
                },
                {
                    path: "time_estimate",
                    returns: '',
                    endpoint: "",
                    input: "mcq",
                    request_input: "question_list",
                    input_description: "mcq Id's",
                    value: [], 
                    value_type: "integer_array",
                    title: "Get the time a question takes to make",
                    response: [],
                    sending: false,
                    send_ids: [],
                    shown: false,
                    is_request: true,
                },
                {
                    'id': 4,
                    'path': 'mcqs_from_array',
                    //what type should the input be: String
                    'input_type': Array,
                    // Name used by function to know how to call the send var
                    'input_name': "idArray",
                    //idk
                    'response_type': 'list of questions with: id, target_id, question_text and answer_options with id, index, text, is_correct, feedback',
                    //return if there was an error (so the site doesn't crash)
                    'empty_response':   [
                                            {
                                            "id": "0",
                                            "target_id": "0",
                                            "question_text": "No Question Text Found",
                                            "answer_options": [
                                                {
                                                "feedback": "",
                                                "is_correct": true,
                                                "text": "No Option Found",
                                                "index": 0,
                                                "id": "0A"
                                                },
                                                {
                                                "feedback": "",
                                                "is_correct": false,
                                                "text": "No Option Found",
                                                "index": 1,
                                                "id": "0B"
                                                },
                                                {
                                                "feedback": "",
                                                "is_correct": false,
                                                "text": "No Option Found",
                                                "index": 2,
                                                "id": "0C"
                                                },
                                                {
                                                "feedback": "",
                                                "is_correct": false,
                                                "text": "No Option Found",
                                                "index": 3,
                                                "id": "0D"
                                                }
                                            ]
                                            }
                                        ],
                    // for the response objects
                    // every different request through this object gets it's own response object, so components don't change other's data
                    //
                    // every response is formatted like '1':{'loading': if the request is still loading, used for loading circle on screen
                    //                                       'input'  : The data that was send to the server, used to know if this request was already called, then it just updates the old one
                    //                                       'data'   : The data.data that the server returned}
                    'response': {},
                    //send with the graphQl server, variables do not currently work
                    'request_data': {"query":`query mcqs_by_id_array($idArray: [ID]!) {
                                                    mcqs_by_id_array(id_array: $idArray) {
                                                            id,
                                                            target_id,
                                                            question_text,
                                                            answer_options {
                                                                id,
                                                                index,
                                                                text,
                                                                is_correct,
                                                                feedback,
                                                            }
                                                            }
                                                        }`,
                                    "variables":{

                                    }},
                    // used to give every new request a unique id
                    'current_request_id': 0,
                    // the returned data goes throught this function before going to the response, if you want to move some data around for performance or efficiecy
                    format_reponse_data: function(data) {
                        try {
                            data.mcqs_by_id_array.forEach((e) => {
                                e.mcqs = e.mcqs.map((e) => {return e.id})
                            });
                            return data.mcqs_by_id_array
                        } catch (error) {
                            return data.mcqs_by_id_array
                        }
                    }
                },
            ],
        }
    },

    computed: {
    },
    methods: {
        forceRender: function(){
            this.render = false;

            this.$nextTick(() => {
                this.render = true;
            })
        },
        totalAnswerDistribution: function(answer_distribution){
            var total = 0
            Object.values(answer_distribution).forEach((e) => {
                total = total + Number(e)
            })
            return total
        },
        // data = {'value' : value that should be formatted, 'value_type': how the value should be formatted}
        // used in: postAnswer
        formatData: function(data) {
            if(data.value_type == 'integer'){
                return parseInt(data.value[0])
            } else if (data.value_type == 'integer_array'){
                return data.value
            } else {
                throw("Invalid query_type type: " + data.value_type)
            }
        },        
        getResponseId: function(name, request_config) {
            const index_of_obj = this.query_objects.findIndex((e) => {return e.path == name})

            if (Object.keys(this.query_objects[index_of_obj].response).map(id => this.query_objects[index_of_obj].response[id].input).lenth == 0){
                return 0
            } else {
                var index = Object.keys(this.query_objects[index_of_obj].response).map(id => this.query_objects[index_of_obj].response[id].input).indexOf(request_config.data)
                if (index != -1) {
                    return Object.keys(this.query_objects[index_of_obj].response)[index]
                } else {
                    this.query_objects[index_of_obj].current_request_id += 1
                    const id = this.query_objects[index_of_obj].current_request_id
                    this.query_objects[index_of_obj].response[id] = {
                        input: {},
                        loading: false,
                        data: this.query_objects[index_of_obj].empty_response
                    }
                    
                    return id
                }
            }
        },
        createRequestConfig: function(name, input) {
            const index_of_obj = this.query_objects.findIndex((e) => {return e.path == name})

            const url = 'https://leerlevels-data-server.herokuapp.com/'
            const headers = { 
                'Authorization': 'Z-@rtN3dpaaa', 
                'Content-Type': 'application/json'
            }

            const request_data = this.query_objects[index_of_obj].request_data
            // const data = JSON.parse(request_data_string)
            input = input || {}
            if (!input instanceof Object) {
                const input_type = this.query_objects[index_of_obj].input_type
                if (input instanceof input_type){
                    if (input_type == Array) {
                        input = [input]
                    }
                    if (input_type == Number) {
                        input = Number(input)
                    }
                    if (input_type == String) {
                        input = input.toString()
                    }
                }
            }
            const key_name = this.query_objects[index_of_obj].input_name
            request_data.variables[key_name] = input

            return {
                url: url,
                method: 'post',
                data: JSON.stringify(request_data),
                headers: headers,
            }
        },
        dataServerRequestApi: async function(config, name, id){
            var index_of_obj = this.query_objects.findIndex((e) => {return e.path == name})

            this.query_objects[index_of_obj].response[id].loading = true
            this.query_objects[index_of_obj].response[id].input = config.data
            console.log('sending: ', name, '\n', ' input: ', JSON.parse(config.data))


            const data = await axios(config).then((response) => {
                console.log('Response: ', name, '\n', ' stored at: ', id, '\n', ' data: ', response.data.data)
                const data = this.query_objects[index_of_obj].format_reponse_data(response.data.data)
                this.query_objects[index_of_obj].response[id].data = data

                this.query_objects[index_of_obj].loading = false
                return data
            })
            return data
        },
        // request the input data from the query object, that call the api server
        // used in: getMcqDataFromArray, getTargetDataFromArray
        apiRequest: async function(name, input){
            const request_config = this.createRequestConfig(name, input)
            
            const id = await this.getResponseId(name, request_config)
            
            const data = await this.dataServerRequestApi(request_config, name, id)
            
            return data
        },
        // send a reuqest from a query obj, and the endpoint
        // used in: vuex store
        postAnswer: function(obj){
            var index_of_obj = this.query_objects.indexOf(obj)

            var endpoint = 'https://leerlevels-data.herokuapp.com/' + obj.path
            var post_object = {}
            post_object[obj.request_input] = this.formatData({'value': obj.value, 'value_type': obj.value_type})
            console.log("Sending: ",post_object, "\n","to: ",endpoint)
            return axios.post(endpoint, post_object).then((response) => {
                console.log("Response of : " + obj.path, '\n', 'response Data: ', response.data)
                this.query_objects[index_of_obj].response = response.data
                return response
            })
            .catch(function(error){
                var full_error = "Verstuurde data: "+JSON.stringify(post_object) +"\n\n"+error
            })
        },
        loadMcqDataFromArray: async function(responseArray){
            responseArray = responseArray.map((e) => {return Number(e)})

            // more data
            const more_mcq_data = await this.apiRequest('mcqs_from_array', responseArray).then((response) => {
                response.forEach((e) => {
                    Object.keys(e).forEach((key) => {
                        this.mcq_data[key] = e[key]
                    })
                })
            })

            // answer distribution
            const distribution_obj_index = this.query_objects.findIndex((e) => {return e.path == 'answer_distribution'})
            this.query_objects[distribution_obj_index].value = responseArray

            const distribution_promise = await this.postAnswer(this.query_objects[distribution_obj_index]).then(() => {
                this.query_objects[distribution_obj_index].response.forEach((e) => {
                    if (e.answer_distribution != 'No distribution for this id'){
                        this.mcq_data.answer_distribution = e.answer_distribution
                    }
                })
            })

            // time estimate data
            const time_estimate_obj_index = this.query_objects.findIndex((e) => {return e.path == 'time_estimate'})
            this.query_objects[time_estimate_obj_index].value = responseArray

            const time_estimate_promise = await this.postAnswer(this.query_objects[time_estimate_obj_index]).then(() => {
                if (this.query_objects[time_estimate_obj_index].response.lenth != 0){
                    this.query_objects[time_estimate_obj_index].response.forEach((e) => {
                        const id = e.id
                        delete e.id
                        this.mcq_data.time_estimate = e
                    })
                }

            })

            return Promise.all([more_mcq_data, distribution_promise, time_estimate_promise])
        }

    },
    created() {
        if (this.obj == null){
            this.loadMcqDataFromArray([this.id]).then(()=>{this.forceRender})
        } else {
            this.mcq_data = this.obj
        }
    },
    watch: {
        obj (new_value){
        this.mcq_data = new_value
        },
        id (){
            this.loadMcqDataFromArray([this.id])
        },


    }
}
</script>

<style>

</style>