<template>
<v-expansion-panel style="width: 100%">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{item_obj.id}}</label>
            </v-col>
            <v-col>
                {{item_obj.label}}
            </v-col>
            <v-col cols="1" v-if="Object.keys(item_obj).includes('needed_question_ratio')">
                <span>{{item_obj.needed_questions}}</span><br>
                <span>Vragen Nodig</span>
            </v-col>
            <v-col cols="1">
            </v-col>
        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
            <v-row v-if="Object.keys(item_obj).includes('needed_question_ratio')">
                <v-col>
                    <span>Ratio: </span><br>
                    <span>Needed Questions: </span><br>
                    <span>Has Questions: </span>
                </v-col>
                <v-col>
                    <span>{{Math.round(item_obj.needed_question_ratio * 10000) / 10000}}</span><br>
                    <span>{{item_obj.needed_questions}}</span><br>
                    <span>{{item_obj.total_questions}}</span>
                </v-col>
            </v-row>
            <!-- if description is an string, show the string, if it's an obj sort and format the obj and show it nicer -->
                <v-expansion-panels>
                    <!-- description -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Beschrijving</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{item_obj.description}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- vraag ids -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Vraag Ids</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{item_obj.mcqs.toString()}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Youtube video -->
                    <v-expansion-panel v-if="Object.keys(item_obj).includes('youtube_id')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Video</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text >
                            <iframe :src="'https://www.youtube.com/embed/' + item_obj.youtube_id"  
                                    allow="accelerometer; 
                                            autoplay; 
                                            encrypted-media; 
                                            gyroscope; 
                                            picture-in-picture" 
                                    style="display: block; height:480px"
                                    width="100%"
                                    
                                    allowfullscreen></iframe>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Slide -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Slide</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <div v-if="Object.keys(item_obj).includes('img_url_large')">
                                <img :src="item_obj.img_url_large" alt="Target Photo" width="300">
                            </div>
                            <v-row  v-if="Object.keys(item_obj).includes('slide_title')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Titel: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0">{{item_obj.slide_title}}</span>
                                </v-col>
                            </v-row>
                            <hr  v-if="Object.keys(item_obj).includes('slide_title') && Object.keys(item_obj).includes('slide_text')">
                            <v-row v-if="Object.keys(item_obj).includes('slide_text')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Text: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0">{{item_obj.slide_text}}</span>
                                </v-col>
                            </v-row>

                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Questions Data -->
                    <v-expansion-panel v-if="Object.keys(item_obj).includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Vragen</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-expansion-panels>
                                <OutputListMcq v-for="(mcq, index) in mcqs" :key="index" :item_obj="mcq" :no_target="true" />
                            </v-expansion-panels>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Tijd Data -->
                    <v-expansion-panel v-if="Object.keys(item_obj).includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Tijd Data</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-table>
                                <thead>
                                    <tr>
                                        <th class="left-text">
                                            <p>Type</p>
                                        </th>
                                        <th class="left-text">
                                            <p>Data</p>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!-- <div v-if="'time_estimate' in item_obj.description"> -->
                                        <tr v-for="(key, index) in Object.keys(time_data)" :key="index">
                                            <td>{{key}}</td>
                                            <td>{{parseFloat(time_data[key]).toFixed(2)}}s</td>
                                        </tr>
                                    <!-- </div> -->
                                </tbody>
                            </v-table>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import {defineAsyncComponent} from 'vue' 

export default {
    name: 'OutputListTarget',
    props: {
        item_obj: Object,
    },
    components: {
        OutputListMcq: defineAsyncComponent(() => import('./OutputListMcq.vue'))
    },
    data (){
        return {
            sorted_description: {},
            view_more_label: false,
        }
    },
    computed: {
        mcqs(){
            return this.item_obj.mcqs.map((e) => {return this.$store.getters.mcqDataFromId(e)})
        },
        time_data(){
            var time_data = {}
            this.mcqs.forEach((e) => {
                if (Object.keys(e).includes('time_estimate')){
                    Object.keys(e.time_estimate).forEach((time_key) => {
                    if (!Object.keys(time_data).includes(time_key)){
                        time_data[time_key] = 0
                    }

                    time_data[time_key] = time_data[time_key] + Math.round(e.time_estimate[time_key])
                    })
                }

            });
            console.log(time_data)
            return time_data
        }
    },
    methods: {
    },
    created() {
        console.log('mcqs', this.item_obj.mcqs)
        this.$store.dispatch('getMcqDataFromArray', this.item_obj.mcqs)
    },
}
</script>

<style>
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    width: 100%;
    font-size: smaller;
    display: inline-block;
}
</style>