<template>
<v-expansion-panel style="width: 100%">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{mcq_data.id}}</label>
            </v-col>
            <v-col>
                {{mcq_data.question_text}}
            </v-col>

        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
        <v-expansion-panels>
            <!-- leerdoel id -->
            <v-expansion-panel>
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel Id:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <p class="box">{{mcq_data.target_id}}</p>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- options -->
            <v-expansion-panel v-if="Object.keys(mcq_data).includes('answer_options')">
                <v-expansion-panel-title class="ma-0">
                    <p>Keuzes:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    #
                                </th>
                                <th class="left-text">
                                    Option
                                </th>
                                <th class="left-text" v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                Aantal
                                </th>
                                <th class="left-text" v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                    %
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(option, index) in mcq_data.answer_options" :key="index" :style="{'background-color': option.is_correct ? '#afff7d': 'white'}">
                                    <td>
                                        <span>{{option.id}}</span>
                                    </td>
                                    <td>{{option.text}}</td>
                                    <td v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                        <span>{{mcq_data.answer_distribution[option.id]}}</span>
                                    </td>
                                    <td v-if="Object.keys(mcq_data).includes('answer_distribution')">
                                        <span class="text-right">{{Math.round(mcq_data.answer_distribution[option.id] / totalAnswerDistribution(mcq_data.answer_distribution) * 100)}}</span>
                                    </td>
                            </tr>

                        </tbody>
                    </v-table>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- tijd data -->
            <v-expansion-panel v-if="Object.keys(mcq_data).includes('time_estimate')">
                <v-expansion-panel-title class="ma-0">
                    <p>Tijd Data</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text>
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    <p>Type</p>
                                </th>
                                <th class="left-text">
                                    <p>Data</p>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- <div v-if="'time_estimate' in mcq_data.description"> -->
                                <tr v-for="(key, index) in Object.keys(mcq_data.time_estimate)" :key="index">
                                    <td>{{key}}</td>
                                    <td>{{parseFloat(mcq_data.time_estimate[key]).toFixed(2)}}s</td>
                                </tr>
                            <!-- </div> -->
                        </tbody>
                    </v-table>
                </v-expansion-panel-text>
            </v-expansion-panel>
            <!-- target -->
            <v-expansion-panel v-if="!no_target">
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-expansion-panels>
                        <TargetPanel :id="item_obj.target_id" />
                    </v-expansion-panels>
                </v-expansion-panel-text >
            </v-expansion-panel>
        </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import TargetPanel from './TargetPanel'

export default {
    name: 'QuestionPanel',
    props: {
        id: Number,
        no_target: {type: Boolean, default: false}
    },
    components: {
        TargetPanel
    },
    computed: {
        mcq_data() {
            return this.$store.getters.mcqDataFromId(this.id)
        }
    },
    methods: {
        totalAnswerDistribution: function(answer_distribution){
            var total = 0
            Object.values(answer_distribution).forEach((e) => {
                total = total + Number(e)
            })
            return total
        }
    },
    mounted() {
        this.$store.dispatch('getMcqDataFromArray', [this.id])
    },
}
</script>

<style>

</style>