<template>
<!-- input box -->
<div>
    <v-text-field variant="outlined"
                            persistent-hint
                            v-on:keydown.enter="clickItem(filteredList[0])" 
                            label="Doorzoek Lijst..." 
                            v-model="search" 
                            hint="Voeg meerdere filters toe met een komma, enter om bovenste te selecteren"></v-text-field>
</div>
<!-- Buttons -->
<div>
    <v-row>
            <v-col >
                <v-card  @click="toggleAll(filteredList.map((e) => {return e.id}))" 
                        class="pa-2 ma-2" 
                        variant="outlined" 
                        :color="buttoncolor">alles in-/uitschakelen</v-card>

                <v-select class="ml-2" 
                            style="width:10em;" 
                            label="Sorteer op:" 
                            density="compact" 
                            outlined 
                            :items="sort_types" 
                            v-model="selected_sort_type"></v-select>
            </v-col>

            <v-col>
                <v-card @click="selectAll(filteredList.map((e) => {return e.id}))" class="ma-2" >
                    <v-checkbox-btn  :model-value="allSelected" label="Selecteer Alles" />
                </v-card>
                <v-card @click="clearAll(filteredList.map((e) => {return e.id}))" class="ma-2" >
                    <v-checkbox-btn :model-value="noneSelected" label="Verwijder Alles" />
                </v-card>
            </v-col>
    </v-row>
</div>
<!-- selected -->
<div>
    <span>{{this.$store.state.datapagina_module.selected_item_ids[selection_type].length}} GESELECTEERD</span>
</div>
<!-- input list -->
<div>
    <v-list @scroll="handleScrollInput" select-strategy="multiple" density="compact" class="scrollable list pa-0" style="resize: vertical; height: 30vh" size="h-50" v-model="selectedItemIds">
        <v-list-item @click="clickItem(filter.id); updateInputBox()" 
                    v-for="(filter, index) in smallerSortedFilteredList" :key="index"
                    :color="buttoncolor"
                    :value="filter.id"
                    class="pa-0 pl-1 ma-0"
                    >
            <v-row >
                <v-list-item-action start style="float: left;">
                    <v-checkbox-btn readonly 
                                    :value="filter.id"  
                                    :color="buttoncolor" 
                                    v-model="selectedItemIds" 
                                    class="pt-1" /> 
                </v-list-item-action>
            </v-row>

            <v-list-item-title class="truncate">{{filter.id}} {{itemText(filter)}}</v-list-item-title>

        </v-list-item>
    </v-list>
</div>
<!-- text field -->
<div>
    <v-text-field v-model="input_box" v-on:input="updateSelectedItemsFromInputBox"/>
</div>
<!-- selected -->
<div>
    <p>Selected: </p>
    <div class="list scrollable" style="height: 150px; resize: vertical; ">
        <v-list select-strategy="multiple" class="pa-0" density="compact">
            <v-list-item v-for="(filter, index) in selected_items[selection_type]" :key="index"
                        :color="buttoncolor"
                        :value="filter.id"
                        @click="clickItem(filter.id)"
                        class="pa-1 ma-1"
                        >
                <span class="truncate">{{filter.id}} {{itemText(filter)}}</span> 
                <v-divider></v-divider>
            </v-list-item>
            <!-- shows the selected items, and removes (because they're already selected) them on click -->
  
        </v-list>    
    </div>
</div>
</template>

<script>
export default {
    name: 'Datapagina',
    props: {
        selection_type: String,
    },
    data (){
        return {
            selected_items: {'target': [], 'mcq': []},
            search: '',
            input_box: "",
            list_length: 10,
            sort_types: ['ID', 'Alfabet'],
            selected_sort_type: 'ID',
            buttoncolor: '#3F816C', 

        }
    },
    computed: {
        // make multisearch available
        searchList() {
            return this.search.toLowerCase().split(',');
        },
        // returns a list out of the store target or mcq data
        // used in: filtered list
        listOfItemType(){
            if (this.selection_type == 'target'){
                return Object.values(this.$store.state.datapagina_module.targets)
            } else 
            if (this.selection_type == 'mcq'){
                return Object.values(this.$store.state.datapagina_module.mcqs)
            }
        },
        // filters the items if 1. nothing is filtered for, 2. the searchtext is in the text, 3. the ids match 
        // used in: 
        filteredList() {
            return this.listOfItemType.filter((item) => {
                if (this.searchList[0] == "" || this.searchList[-1] == " ") {
                    return true
                } else 
                if (this.searchList.some((searchItem) => {return this.itemText(item).toLowerCase().includes(searchItem) && searchItem !== ''})) {
                    return true
                } else 
                if (this.searchList.some((searchItem) => item.id == searchItem)) {
                    return true
                } else return false
            }
            );
        },
        // sorts the filtered list on the current sort type: ID, alphabet
        // used in: 
        sortedFilteredList(){
            if (this.selected_sort_type == 'ID'){
                return this.filteredList.sort((a, b) => a.id - b.id)
            }
            if (this.selected_sort_type == 'Alfabet'){
                return this.filteredList.sort((a, b) => {
                    const a_label = this.selection_type == 'target' ? a.label : a.question_text
                    const b_label = this.selection_type == 'target' ? b.label : b.question_text
                    if ( a_label < b_label ){
                        return -1;
                    }
                    if ( a_label > b_label ){
                        return 1;
                    }
                    return 0;
                })
            }
        },
        // makes the sorted list smaller, for performance
        // used in: template
        smallerSortedFilteredList(){
            return this.sortedFilteredList.slice(0, this.list_length)
        },

        // returns true if every item in the filtered list is selected
        // used in: toggleAll(), template
        allSelected: {
            get() {return this.filteredList.every((filtered_item) => this.selected_items[this.selection_type].includes(filtered_item)) },
            set(val){}
        },
        // return true if all filtered items are deslected
        // used in: template
        noneSelected: {
            get() {return this.filteredList.every((filtered_item) => !this.selected_items[this.selection_type].includes(filtered_item)) },
            set(val){}
        },

        // returns every id selected
        // used in: template
        selectedItemIds: {
            set(value){this.$store.commit('setSelectedItemIdsFromInputBox', {value: value, type: this.selection_type}); this.updateInputBox()},
            get(){return this.$store.state.datapagina_module.selected_item_ids[this.selection_type]}
        },
        // checks id the selected items was changed in another component
        // used in: selectedItemsUpdated (watch)
        selectedItemsUpdated: function(){
            return this.$store.state.datapagina_module.selected_items_updated[this.selection_type]
        },
        // returns the selected items
        // used in: selectedItems (watch)
        selectedItems: function(){
            return this.$store.getters.selectedItems
        },
    },
    methods: {
        // updates input box
        // used in: selectedItemsUpdated (watch)
        updateInputBox(){
            this.input_box = this.$store.state.datapagina_module.selected_item_ids[this.selection_type]
        },
        // if the input box changes, change selected items
        updateSelectedItemsFromInputBox: function(){
            this.$store.commit('setSelectedItemIdsFromInputBox', {value: this.input_box.split(','), 
                                                      type: this.selection_type})
        },
        // if not all selected, select all, if all selected, deselect all (filtered list)
        // used in: template
        toggleAll: function(ids) {
            const all_selected = this.allSelected
            if (all_selected){
                this.$store.commit('clearAll', {'ids':ids, 'type': this.selection_type})
            } else {
                this.$store.commit('selectAll', {'ids':ids, 'type': this.selection_type})
            }
        },
        // select all filtered ids 
        // used in: template
        selectAll: function(ids) {
            this.$store.commit('selectAll', {'ids':ids, 'type': this.selection_type})
        },
        // deselect all filtered ids 
        // used in: template
        clearAll: function(ids) {
            this.$store.commit('clearAll', {'ids':ids, 'type': this.selection_type})
        },
        // selects if deselected, deselect if selected
        // used in: template 
        clickItem: function(id){
            this.$store.commit('clickItem', {id: id, type: this.selection_type}); 
        },

        // if scrolled down in selection window, load more
        handleScrollInput: function({ target: { scrollTop, clientHeight, scrollHeight }}) {
            if (scrollTop + clientHeight + 40 >= scrollHeight) {
                this.getNewInputItems()
            }
        },
        // make selection list longer
        // used in: handleScrollInput()
        getNewInputItems: function(){
            this.list_length += 10
        },

        // returns the label text if target, else return question text
        itemText: function(item){
            return this.selection_type == 'target' ? item.label : item.question_text
        },
    },
    watch: {
        // updates the input box if the selected items updated
        selectedItemsUpdated (new_value, old_value) {
                this.updateInputBox()
                this.$store.commit('setSelectedItemsUpdated', {value: false, type:this.selection_type})
        },
        // copy the selected items locally, so it doesn't have to reload if you change something
        selectedItems (new_value, old_value){
            this.selected_items = new_value
        }
    }
}
</script>

<style scoped>
.small-box{
  float: left; 
  margin-left: 10px; 
  box-sizing: content-box; 
  border: 2px solid steelblue;
  padding-left: 3px;
  padding-right: 3px;
background: lightsalmon;
}
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;
}
.list-item{
    background: none;
}
.list-item:hover{
    background: '#3F816C',
}
</style>