<template>
<v-row>
        <v-col cols="12" md="4" sm="12">
            <v-card variant="outlined" class="ma-2">
                <v-card-text class="pa-0">
                    <v-toolbar density="compact" color="#dcdcdc" class="ma-0" fixed>
                        <v-toolbar-title>Fotos</v-toolbar-title>
                        <template v-slot:extension>
                            <div style="margin-bottom: 10px">
                            <v-tabs align-with-title v-model="image_tab" style="height: min-content;" show-arrows centered center-active>
                                <v-tab v-for="(photo, index) in image_previews" 
                                        :key="index"
                                        :value="image_list[index].name">
                                    <span class="truncate" style="width: 105px; font-size: smaller;">{{image_list[index].name}}</span>
                                    <v-icon right @click="removePhoto(index)" color="red" large>mdi-close</v-icon>
                                </v-tab>
                            </v-tabs>
                        </div>
                        </template>
                    </v-toolbar>

                    <!-- Image -->
                    <v-card class="overflow-y-auto">
                        <v-card-text>
                            <v-window v-model="image_tab">
                                <v-window-item v-for="(photo, index) in image_previews"
                                                :key="index"
                                                :value="image_list[index].name">
                                    <img :src="photo" 
                                        :alt="image_list[index].name" 
                                        
                                        >
                                </v-window-item>
                            </v-window>
                        </v-card-text>

                    </v-card>

                </v-card-text>

            </v-card>
            <v-row>
                <v-col cols="8">
                    <v-file-input 
                    accept="image/*" 
                    label="Upload Image"
                    class="ma-1 pa-0"
                    prepend-icon="mdi-camera"
                    variant="outlined"
                    multiple
                    v-model="uploaded_photos"
                    @change="filesUploaded"
                    type="file"
                    ></v-file-input>
                </v-col>
                <v-col cols="4">
                    <v-btn :disabled="image_list.length == 0" @click="removeAllPhotos" class="ma-1 float-right"><span>Remove All</span></v-btn>
                </v-col>
            </v-row>
            <v-btn @click="requestImageData(imageFileFromName(image_tab))"  class="ma-2 pa-0"><span>Vraag Data Opniew Op</span></v-btn>
        </v-col>

        <v-col cols="12" md="8" sm="12">
            <v-card class="fill-height ma-2"  variant="outlined">
                <v-toolbar fixed density="compact">
                    <v-toolbar-title><span>{{image_tab}}</span></v-toolbar-title>
                    <!-- <template v-slot:extension>
                        <div style="margin-bottom: 3px;">
                            <v-tabs v-model="image_info_tab" centered center-active show-arrows align-with-title>
                                <v-tab v-for="(key, index) in Object.keys(returned_image_data)" :key="index" :value="key">
                                    <span class="truncate" style="width: 110px;font-size: smaller;">{{key}}</span>
                                </v-tab>
                            </v-tabs>
                        </div>
                    </template> -->
                </v-toolbar>
                <v-card-text class="pa-2">
                    <!-- <v-window v-model="image_info_tab">
                    <v-window-item v-for="(key, index) in Object.keys(returned_image_data)" 
                                    :key="index" 
                                    :value="key"> -->
                        <v-row class="fill-height">
                            <div v-if="Object.keys(returned_image_data).length !== 0" >
                            <!-- <v-col cols="4">
                                    <img :src="image_previews[Object.keys(returned_image_data).indexOf(image_tab)]" :alt="returned_image_data[image_tab]">

                            </v-col> -->
                            <v-col cols="12">

                                <v-card  class="overflow-y-auto">
                                    <v-progress-circular v-if="Object.keys(returned_image_data[image_tab]).length === 0" indeterminate></v-progress-circular>
                                    <v-list v-else class="overflow-y-auto">
                                        <v-expansion-panels multiple variant="accordion" v-model="opened_image_panels">
                                            <!-- targets id -->
                                            <v-expansion-panel value="target_ids">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Bijbehorende Leerdoelen</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <v-expansion-panels>
                                                        <v-table>
                                                            <thead>
                                                                <tr>
                                                                    <th class="text-left">
                                                                        <span>Leerdoel ID</span>
                                                                    </th>
                                                                    <th class="text-left">
                                                                        <span>Label</span>
                                                                    </th>   
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <tr v-for="(target_id, index) in returned_image_data[image_tab].top_k_id" :key="index">
                                                                    <td>
                                                                        <span>{{target_id}}</span>
                                                                    </td>
                                                                    <td>
                                                                        <span>{{this.$store.getters.targetDataFromId(target_id).label}}</span>
                                                                    </td>
                                                                </tr>
                                                            </tbody>
                                                        </v-table>
                                                    </v-expansion-panels>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                            <!-- Original Text -->
                                            <v-expansion-panel  value="original_text">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Originele Tekst</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <span class="truncate">{{returned_image_data[image_tab].original_text}}</span>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                            <!-- Questions -->
                                            <v-expansion-panel value="questions">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Bijbehorende Vragen</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <v-expansion-panels>
                                                        <v-expansion-panel v-for="(title, index) in returned_image_data[image_tab].top_k_question" :key="index">
                                                            <v-expansion-panel-title>
                                                                <span>{{title}}</span>
                                                            </v-expansion-panel-title>
                                                            <v-expansion-panel-text>
                                                                <span>{{title}} <br> data</span>
                                                            </v-expansion-panel-text>
                                                        </v-expansion-panel>
                                                        <!-- <QuestionPanel  v-for="(id, index) in returned_image_data[key].top_k_question" :key="index" :id="id"></QuestionPanel> -->
                                                    </v-expansion-panels>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                            <!-- targets -->
                                            <v-expansion-panel value="targets">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Bijbehorende Leerdoelen</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <v-expansion-panels>
                                                        <TargetPanel v-for="(id, index) in noDuplicateArray(returned_image_data[image_tab].top_k_id)" :key="index" :id="id"></TargetPanel>

                                                    </v-expansion-panels>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                            <!-- top_k_documents -->
                                            <v-expansion-panel value="tok_k_documents">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Top Documents</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <span>{{returned_image_data[image_tab].top_k_documents}}</span>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                            <!-- data -->
                                            <v-expansion-panel value="data">
                                                <v-expansion-panel-title>
                                                    <span class="truncate">Data</span>
                                                </v-expansion-panel-title>
                                                <v-expansion-panel-text>
                                                    <span>{{returned_image_data[image_tab]}}</span>
                                                </v-expansion-panel-text>
                                            </v-expansion-panel>
                                        </v-expansion-panels>
                                    </v-list>

                                </v-card>

                            </v-col>
                        </div>

                        </v-row>
                    <!-- </v-window-item>
                </v-window>
                 -->
                </v-card-text>


            </v-card>
        </v-col>
    </v-row>
</template>

<script>
import axios from 'axios'
import QuestionPanel from './components/QuestionPanel'
import TargetPanel from './components/TargetPanel'
export default {
    name: 'FotoInfo',
    components: {
        QuestionPanel,
        TargetPanel
    },
    data (){
        return {
            image_previews: [],
            image_list: [],
            uploaded_photos: [],
            image_tab: '',
            image_info_tab: '',
            opened_image_panels: ['target_ids'],
            returned_image_data: {},
        }
    },
    methods: {
        filesUploaded: function(event) {
            var input = event.target;
            var count = input.files.length;
            var index = 0;
            if (input.files) {
                while(count --) {
                var reader = new FileReader();
                if (!this.imageNames.includes(input.files[index].name)){
                    reader.onload = (e) => {
                    this.image_previews.push(e.target.result);
                    }
                    this.image_list.push(input.files[index]);
                    this.returned_image_data[input.files[index].name] = {}
                    this.requestImageData(input.files[index])
                    reader.readAsDataURL(input.files[index]);
                }

                index ++;
                }
            }
            this.uploaded_photos = []
            this.image_tab = this.image_info_tab = input.files[input.files.length - 1].name
        },
        removePhoto: function(index){
            if (index != -1){
                this.image_previews.splice(index, 1)
                this.image_list.splice(index, 1)
            }
        }, 
        removeAllPhotos: function(){
            this.image_list = [];
            this.preview_list = [];
        } ,
        requestImageData: function(file){
            var formData = new FormData()
            var imagefile = file
            formData.append("image", imagefile)
            console.log('sending >> formatData >> ', formData)
            axios.post('https://leerlevels-opdrachtscan.herokuapp.com/', 
                    formData, 
                    {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    }).then((response) => {
                        console.log('Image Response: ',response)
                        this.returned_image_data[file.name] = response.data
                    }).catch((error) => {console.log(error)})

        },
        imageFileFromName(name){
            const index = this.imageNames.indexOf(name)
            if (index != -1){
                return this.image_list[index]
            }
        },
        noDuplicateArray: function(array){
            return array.filter(function(item, pos) {
                return array.indexOf(item) == pos;
            })
        } 
    },
    computed: {
        imageNames (){
            return this.image_list.map(e => e.name)
        },

    },
    watch:{
        image_tab: function(new_value, old_value) {
            this.opened_image_panels = ['target_ids']

        }
    },
    mounted() {
        this.$store.dispatch('loadTargets')
        this.$store.dispatch('loadMcqs')
    },
}
</script>

<style>
span {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    font-size: 93%;
}
</style>