<template>
    <div v-if="dataLoaded == false">
        <v-progress-circular indeterminate></v-progress-circular>
    </div>
    <div v-else>
    <v-row class="ma-5">
        <v-col cols="6">
            <v-text-field variant="outlined"
                        persistent-hint
                        v-on:keydown.enter="clickItem(filteredList[0])" 
                        label="Doorzoek Lijst..." 
                        v-model="search" 
                        hint="Voeg meerdere filters toe met een komma, enter om bovenste te selecteren"></v-text-field>
            <v-btn @click="getLLSearch(search)">Request Data</v-btn>
            <div>
                send string: 
            {{last_requested}}
            </div>
            Items: 
            <v-list class="box list scrollable">
                <v-list-item v-for="(item, index) in item_data" :key="index"  @click="selectItem(item)">
                    <v-list-item-title v-if="item.type == 'target'">
                        <v-row>
                            <v-col cols="1">
                                <span>T{{item.data.id}}</span>
                            </v-col>
                            <v-col>
                                <span>  {{item.data.label}}</span>

                            </v-col>
                        </v-row>
                    </v-list-item-title>
                    <v-list-item-title v-else>
                        <v-row>
                            <v-col cols="1">
                                <span>Q{{item.data.id}}</span>
                            </v-col>
                            <v-col>
                                <span>  {{item.data.question_text}}</span>

                            </v-col>
                        </v-row>
                    </v-list-item-title>
                </v-list-item>
            </v-list>


        </v-col>
        <v-col cols="6">
            <v-card>
                <div v-if="selected.type != ''">
                    <div v-if="selected.type == 'target'">
                        <v-expansion-panels v-model="panel">
                            <TargetPanel :id="selected.id" />

                        </v-expansion-panels>
                    </div>
                    <div v-else>
                        <v-expansion-panels v-model="panel">
                            <QuestionPanel :id="selected.id" />
                        </v-expansion-panels>
                    </div>
                </div>
            </v-card>
        </v-col>

    </v-row>
</div>

</template>

<script>
import axios from 'axios'
import QuestionPanel from './components/QuestionPanel'
import TargetPanel from './components/TargetPanel'

export default {
    name: 'SearchItems',
    components: {
    QuestionPanel,
    TargetPanel,
    TargetPanel,
    QuestionPanel
},
    data (){
        return {

            search: 'test',
            response: {"status": false, "query": "", "top_k_id": []},
            last_requested: '',
            selected: {
                'id': '',
                'type': ''
            },
            panel: [0]

        }
    },
    computed: {
        item_data(){
            const data = []
            this.response.top_k_id.forEach((e) => {
                if (this.splitId(e).type == 'target'){
                    data.push({
                        'type': 'target', 
                        'data': this.$store.getters.targetDataFromId(this.splitId(e).id)
                    })
                }
                if (this.splitId(e).type == 'question'){
                    data.push({
                        'type': 'question', 
                        'data': this.$store.getters.mcqDataFromId(this.splitId(e).id)
                    })
                }
            })
            return data
        },
        dataLoaded(){
            return  [Object.keys(this.$store.state.datapagina_module.targets).length != 0, 
                    Object.keys(this.$store.state.datapagina_module.mcqs).length != 0].every(e => e === true)
        },

    },
    methods: {
        getLLSearch: async function(string){
            const data = {
                "query": string.toString()
            }
            console.log('Sending: ', string, '\n', 'to: ', 'https://leerlevels-opdrachtscan.herokuapp.com/LL_search_engine')
            const response = axios.post('https://leerlevels-opdrachtscan.herokuapp.com/LL_search_engine', data).then((response) => {
                console.log(response)
                this.response = response.data
                this.last_requested = string
                return response
            })
            return response
        },
        splitId: function(id){
            const data ={}
            data.type = id.slice(0,1) == 'T' ? 'target' : 'question'
            data.id = id.slice(1)
            return data
        },

        selectItem: function(item){
            this.selected = {'id':item.data.id, 'type': item.type}
        }
    },

}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;  
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
span {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    font-size: 93%;
}
</style>